/**
 * api接口统一管理
 */

import axios from './http'; // 导入http中创建的axios实例
// import qs from 'qs'; // 根据需求是否导入qs模块
const api_url = {
  hosturl: 'http://127.0.0.1:8000',
}

const ajax_obj = {
  search_by_name(params) {
    return axios.get(`${api_url.hosturl}/v1/search_by_name/`+params);
  },
  search_by_build_name(params) {
    return axios.get(`${api_url.hosturl}/v1/search_by_build_name/`+params);
  },
  get_over_view(){
    return axios.get(`${api_url.hosturl}/v1/over_view`)
  }

}
export default ajax_obj;
